.is-hidden {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
}

.breed-select {
    display: block;
}

.cat-info-card {
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    padding: 10px;
    display: flex;
    gap: 20px;
}

img {
    width: 40%;
}

.is-hidden {
    display: none;
}

.breed-name {
    text-align: center;
    margin-bottom: 10px;
    color: blueviolet
}

.temp-title {
    font-weight: 600;
}

.breed-temp {

    text-align: center;
}

.loader {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    border: 2px solid violet;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
}

.loader::after {
    content: '';
    box-sizing: border-box;
    position: absolute;
    left: 4px;
    top: 4px;
    border: 2px solid black;
    width: 12px;
    height: 12px;
    border-radius: 50%;
}

@keyframes rotation {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}